import { useState } from 'react';
import './Home.css';
import { Link } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer';
import Menu from '../Menu/Menu';

const Home = () => {
    const [menuActive, setMenuActive] = useState<boolean>(false);
    return (
        <div className="home">
            <Header menuActive={menuActive} setMenuActive={setMenuActive} />
            <Menu menuActive={menuActive} setMenuActive={setMenuActive} />
            <div className="wrapper row2 bgded">
                <div className="overlay">
                    <h2></h2>
                    <div id="intro" className="clear">
                        <article className="one_third first">
                            <h3 className="heading"></h3>
                            <ul className="nospace">
                                <li>  : </li>
                                <li>-  </li>
                                <li>- </li>
                                <li></li>
                                <li></li>
                            </ul>
                            <p className="link">
                                <Link to="/bookInfo"> &raquo;</Link>
                            </p>
                        </article>
                        <article className="one_third">
                            <h3 className="heading"></h3>
                            <ul className="nospace">
                                <li>   :</li>
                                <li>-    </li>
                                <li>-    </li>
                                <li>-    </li>
                            </ul>
                            <p className="link">
                                <Link to="/statInfo"> &raquo;</Link>
                            </p>
                        </article>
                        <article className="one_third">
                            <h3 className="heading"> </h3>
                            <ul className="nospace">
                                <li>    :</li>
                                <li>-  </li>
                                <li>-  </li>
                                <li>-      </li>
                            </ul>
                            <p className="link">
                                <Link to="/gameInfo"> &raquo;</Link>
                            </p>
                        </article>
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    );
};

export default Home;
